import 'react-app-polyfill/ie11';
import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { WorldDaylightMap, Options } from '../.';
// const WorldDaylightMap = require('../dist').default;
import './styles.css';

const options: Options = {
  width: 600,
  height: 300,
  controlsPosition: 'top',
  controlsScale: 1,
  font: "'Roboto', sans-serif",
  fontSize: undefined,
  isSunshineDisplayed: !true,
  icons: [
    {
      iconLabel: 'London',
      iconUrl: 'icon-computer.png',
      iconCoord: { lat: 51.507222, lng: -0.1275 },
      iconLink: 'https://en.wikipedia.org/wiki/London',
      iconToSvgWidthRatio: 1 / 10,
      // iconWidth: 100, // Overrides iconToSvgWidthRatio
      // iconHeight: 200, // Overrides iconToSvgWidthRatio
    },
  ],
};

const App = () => {
  return (
    <div style={{ width: '800px', height: '400px' }}>
      <WorldDaylightMap options={options} />
    </div>
  );
};

ReactDOM.render(<App />, document.getElementById('root'));
